@import url(https://fonts.googleapis.com/css2?family=Montaga&family=Roboto+Slab:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Caladea&display=swap);
@import url(https://fonts.googleapis.com/css?family=Spartan&display=swap);
@import url(https://fonts.googleapis.com/css?family=Spartan&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montaga&family=Roboto+Slab:wght@900&display=swap);
.navLink {
  font-family: "Montaga", serif;
  font-size: 20px;
  z-index: 10;

  color: #222222;
}
.navLink:hover {
  -webkit-text-decoration: underline #ff9163;
          text-decoration: underline #ff9163;
  color: #ff9163;
}
@media only screen and (max-width: 600px) {
  .nav-bar {
    display: flex;
    justify-content: center;
  }
}

.delete-icon {
  font-size: 1em;
  align-items: center;
  cursor: pointer;
  color: rgba(128, 128, 128, 0.582);
}

.book-card h2 {
  font-family: Montaga;
}

.search-box {
  margin-top: 6vh;
  align-items: center;
}

.bookQuote {
  margin-top: 10px;
  font-size: 16px;
  font-style: italic;
  color: #757474;
  font-family: Spartan;

  padding: 10px;
}

.search-label {
  font-family: "Spartan";
  font-size: 25px;
  letter-spacing: 0px;
}

.search-button {
  border: 2px solid #ff0a87;
  box-shadow: 0px 2px 4px rgba(121, 121, 121, 0.616);
  background-color: inherit;
  font-weight: 600;
  border-radius: 20px;
  font-size: 14px;

  vertical-align: middle;

  font-family: "Spartan";
}

.fa-search {
  font-size: 1.2em;
  color: #ff0a87;
  padding: 5px;
  text-align: left;
}

.search-button:hover {
  background-color: #ff0a8960;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.input-row input {
  background-color: white;
}

input[type="text"] {
  background: #f1f1f1;
}

@media only screen and (max-width: 600px) {
  .search-label {
    font-family: "Spartan";
    font-size: 18px;
  }

  .search-button {
    margin-top: 5px;
    margin: auto;
  }
  .bookQuote {
    font-size: 13px;
  }
}

.reader-img {
  height: auto;
  width: 100%;
}

.hero-text h4 {
  font-family: Montaga;
  font-size: 40px;
}

.hero-container {
  margin-top: 10vh;
}

@media only screen and (max-width: 600px) {
  .hero-text h4 {
    font-family: Montaga;
    font-size: 30px;
    text-align: center;
    margin-bottom: 5vh;
  }
}

.search-icon {
  display: flex;
  background-color: inherit;
  align-items: center;
  border: none;
  cursor: pointer;
}

.fa-search {
  color: #ff9163;
}
.book-input {
  background-color: white !important;
  position: absolute;

  font-family: Montaga;
  font-size: 14px;
}

.form-control:focus {
  border-color: rgba(128, 128, 128, 0.397);
  box-shadow: none;
}

.book-in-db {
  font-family: "Montaga", serif;
  color: #8a8a8a;
  text-align: center;
  font-size: 14px;
}

.book-item {
  font-family: "Montaga", serif;
  display: flex;
  padding: 20px 10px 20px 10px;
  flex-direction: column;
  border-bottom: 1px solid #8a8a8a28;
  margin-bottom: 20px;
  align-items: center;
  grid-gap: 20px;
  grid-gap: 20px;
  gap: 20px;
  background-color: #ff926312;
  border-radius: 5px;
}

.book-item div:nth-child(2) {
  display: flex;
  flex-direction: column;
}

.book-item img {
  vertical-align: middle;
  border-style: none;
  width: 100%;
  height: auto;
  width: 160px;
  margin: auto;
}

.book-card {
  margin-top: 10vh;
}

.book-card h5 {
  font-weight: bold;
  font-size: 18px;
  color: black;
}

.book-card h6 {
  font-size: 16px;
  color: rgba(128, 128, 128, 0.87);
}

.book-card p {
  font-size: 14px;
  color: rgba(24, 24, 24, 0.87);
}

.book-description {
  font-size: 15px;
  color: #363636;
}
.hide {
  display: none;
}
.save-button {
  background-color: inherit;
  border: 1px solid silver;
  border-radius: 5px;
  color: gray;
  cursor: pointer;
  text-align: center;
  font-size: 14px;
  padding: 10px;
}
.save-button:hover {
  background-color: #ff9163;
  -webkit-transition: 1s;
  transition: 1s;
  color: white;
}

.show {
  display: block;
}

.fa-heart {
  cursor: pointer;
}

.fa-heart:hover {
  color: red;
}

.modal-body-bookCard {
  font-family: "Caladea";
  font-size: 14px !important;
  letter-spacing: 0.5px;
  padding: 20px;
}

.modal-header-bookCard {
  padding: 5px !important;
}

@media screen and (min-width: 800px) {
  .book-item {
    flex-direction: row;
    grid-gap: 20px;
    gap: 20px;
  }

  .book-item div:nth-child(2) {
    display: block;
  }
}

